
.section {
    opacity: 0; /* 처음에는 보이지 않음 */
    transform: translateY(50px); /* 아래에서 위로 이동 */
    transition: opacity 1.2s ease, transform 1.2s ease; /* 1.2초로 느리게 */
    padding: 5vw 5%;
    display: flex;
    flex-wrap: wrap; /* 좁은 화면에서 줄바꿈 */
    padding-left: 21%;
    align-items: center;
    justify-content: space-between;
    padding-top: 100px;
  }
  
  .section.visible {
    opacity: 1; /* 요소가 보이도록 */
    transform: translateY(0); /* 원래 위치로 이동 */
  }
  
  .text {
    margin-bottom: 20px;
  }
  
  .image-container {
    display: flex;
    justify-content: center;
    gap: 60px; /* 이미지 간 간격 */
  }
  
  .section-image {
    width: 160px; /* 기본 크기 설정 */
    height: auto;
    object-fit: contain;
    margin-left: -20%; /* 여백 조절 */
    margin-right: 20%; /* 여백 조절 */
    margin-top: 2.1%;
    transform: scale(1.77); 
    transition: transform 0.5s ease; /* 확대 효과 추가 */
}

.section-image2 {
  width: 160px; /* 기본 크기 설정 */
  height: auto;
  object-fit: contain;
  margin-left: -13%; /* 여백 조절 */
  margin-right: 18%; /* 여백 조절 */
  transform: scale(1.51); 
  transition: transform 0.5s ease; /* 확대 효과 추가 */
}

  .section-image3 {
      height: auto;
      object-fit: contain;
      margin-left: -21%; /* 여백 조절 */
      margin-right: 20%; /* 여백 조절 */
      transform: scale(1.12); 
      transition: transform 0.5s ease; /* 확대 효과 추가 */
      width: 280px; /* 기본 크기 설정 */
  }

  .section-image4 {
    width: 160px; /* 기본 크기 설정 */
    height: auto;
    object-fit: contain;
    margin-left: -16%; /* 여백 조절 */
    margin-right: 26%; /* 여백 조절 */
    transform: scale(1.1); 
    transition: transform 0.5s ease; /* 확대 효과 추가 */
    width: 360px; /* 기본 크기 설정 */
}
  
  .section .text {
    max-width: 600px;
  }
  
  .section button {
    margin-top: 10px;
    padding: 10px 15px;
    background-color: #00CA9A;
    color: white;
    border: none;
    cursor: pointer;
  }
  
/* 반응형 디자인 추가 */
/* 화면 크기가 1000px 이하일 때 (모바일 크기) */
@media screen and (max-width: 1000px) {
  .section {
    flex-direction: column; /* 세로 정렬 */
    align-items: center; /* 섹션 내용을 가운데 정렬 */
    text-align: center; /* 텍스트를 중앙 정렬 */
    padding-top: 100px;
    padding-left: 10%; /* 양쪽 여백 줄이기 */
  }

  .section .text {
    margin-bottom: 20px; /* 아래 간격 */
    max-width: 100%; /* 텍스트가 섹션 너비에 맞게 조정 */
  }

  .image-container {
    width: 100%; /* 부모 컨테이너의 너비를 100%로 설정 */
    display: flex;
    max-width: none; /* 최대 너비 제한 해제 */
    justify-content: center;
    gap: 20px; /* 이미지 간 간격 */
  }
  
  .section-image{
        max-width: 100%; /* 화면 크기에 맞추기 */
        height: auto; /* 비율 유지 */
        margin-left: 0; /* 여백 제거 */
        margin-right: 10px; /* 여백 제거 */
        transform: scale(1.3); 
    }

    .section-image2{
      max-width: 100%; /* 화면 크기에 맞추기 */
      height: auto; /* 비율 유ㅛ지 */
      margin-left: 0; /* 여백 제거 */
      margin-right: 10px; /* 여백 제거 */
      transform: scale(1.11); 
  }

    .section-image3 {
      max-width: 100%; /* 화면 크기에 맞추기 */
      height: auto; /* 비율 유지 */
      margin-left: 0; /* 여백 제거 */
      margin-right: 0; /* 여백 제거 */
      transform: scale(1); 
  }

    .section-image4{
      max-width: 100%; /* 화면 크기에 맞추기 */
      height: auto; /* 비율 유지 */
      margin-left: 0; /* 여백 제거 */
      margin-right: 10px; /* 여백 제거 */
      transform: scale(1); 
  }
}